
'use client';

import { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card'; // Import CardFooter
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Video, VideoOff, AlertCircle, Loader2, Copy, LogIn } from 'lucide-react';
import { useTranslation } from 'react-i18next';
import Link from 'next/link';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"

// Simple unique code generation (replace with a more robust solution if needed)
function generateMeetingCode(): string {
  return Math.random().toString(36).substring(2, 8).toUpperCase();
}

export default function VideoCallPage() {
  const router = useRouter();
  const { user, loading: authLoading } = useAuth();
  const { toast } = useToast();
  const { t } = useTranslation();
  const videoRef = useRef<HTMLVideoElement>(null);
  const [hasCameraPermission, setHasCameraPermission] = useState<boolean | null>(null); // null initially, true/false after check
  const [isMeetingActive, setIsMeetingActive] = useState(false);
  const [meetingCode, setMeetingCode] = useState('');
  const [showJoinModal, setShowJoinModal] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [generatedCode, setGeneratedCode] = useState<string | null>(null);

  const requestCameraPermission = async () => {
     setIsLoading(true);
     try {
       const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true }); // Request both audio and video
       setHasCameraPermission(true);
       if (videoRef.current) {
         videoRef.current.srcObject = stream;
       }
       console.log("Camera access granted.");
       // Automatically start/join the meeting once permission is granted
       if(generatedCode) { // If a code was generated (host started)
           handleStartMeeting(generatedCode);
       } else if (meetingCode) { // If a code was entered (joining)
           handleJoinMeeting();
       }
       // If neither, permission is granted but user hasn't clicked start/join yet

     } catch (error) {
       console.error('Error accessing camera/microphone:', error);
       setHasCameraPermission(false);
       toast({
         variant: 'destructive',
         title: t('page.videoCall.error.permissionDeniedTitle'),
         description: t('page.videoCall.error.permissionDenied'),
       });
     } finally {
         setIsLoading(false);
     }
   };


  // Effect to request permission only if needed and meeting is intended to start
  useEffect(() => {
     // Only request if we intend to start a meeting and permission status is unknown
    if ((isMeetingActive || meetingCode || generatedCode) && hasCameraPermission === null) {
      requestCameraPermission();
    }

    // Cleanup function to stop media stream when component unmounts or meeting ends
    return () => {
        if (videoRef.current && videoRef.current.srcObject) {
            const stream = videoRef.current.srcObject as MediaStream;
            stream.getTracks().forEach(track => track.stop());
            console.log("Media stream stopped.");
        }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isMeetingActive, meetingCode, generatedCode]); // Rerun if meeting state changes

  const handleJoinMeeting = () => {
    if (!meetingCode.trim()) {
      toast({ variant: 'destructive', title: t('page.videoCall.error.codeRequired') });
      return;
    }
    // Basic validation / permission check
    if (hasCameraPermission === null) {
        // Request permission first, the effect will handle joining after grant
        requestCameraPermission();
        return;
    }
    if (hasCameraPermission === false) {
        toast({ variant: 'destructive', title: t('page.videoCall.error.permissionDeniedTitle'), description: t('page.videoCall.error.permissionRequired') });
        return;
    }

    console.log(`Joining meeting with code: ${meetingCode}`);
    // In a real app, you'd validate the code and connect to a WebRTC service
    setIsMeetingActive(true);
    setShowJoinModal(false); // Close modal on successful join attempt
    toast({ title: t('page.videoCall.toast.joiningTitle'), description: t('page.videoCall.toast.joiningDescription', { code: meetingCode }) });
    // If permission was already granted, video stream should be active via useEffect
  };

  const handleGenerateCode = () => {
    const newCode = generateMeetingCode();
    setGeneratedCode(newCode);
    // Request permission immediately after generating code
    requestCameraPermission();
  };

  const handleStartMeeting = (code: string) => {
      if (hasCameraPermission === null) {
         // Permission request is already in progress via handleGenerateCode -> requestCameraPermission
         return;
      }
      if (hasCameraPermission === false) {
         toast({ variant: 'destructive', title: t('page.videoCall.error.permissionDeniedTitle'), description: t('page.videoCall.error.permissionRequired') });
         setGeneratedCode(null); // Reset generated code if permission denied
         return;
      }

      console.log(`Starting new meeting with code: ${code}`);
      setMeetingCode(code); // Set the meeting code state
      setIsMeetingActive(true);
      toast({ title: t('page.videoCall.toast.startedTitle'), description: t('page.videoCall.toast.startedDescription', { code: code }) });
      // Video stream should be active via useEffect
  };


  const handleLeaveMeeting = () => {
    setIsMeetingActive(false);
    setMeetingCode('');
    setGeneratedCode(null);
    if (videoRef.current && videoRef.current.srcObject) {
      const stream = videoRef.current.srcObject as MediaStream;
      stream.getTracks().forEach(track => track.stop());
      videoRef.current.srcObject = null; // Clear the srcObject
    }
    // Reset permission state to trigger request again if needed
    // setHasCameraPermission(null); // Or keep it true if granted? Decide based on desired UX. Let's keep it true.
    toast({ title: t('page.videoCall.toast.leftMeeting') });
  };

  const copyCodeToClipboard = () => {
    if (generatedCode) {
      navigator.clipboard.writeText(generatedCode)
        .then(() => {
          toast({ title: t('page.videoCall.toast.codeCopied') });
        })
        .catch(err => {
          console.error('Failed to copy meeting code: ', err);
          toast({ variant: 'destructive', title: t('page.videoCall.error.copyFailed') });
        });
    }
  };

  // Loading state for auth
  if (authLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-200px)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  // Redirect if not logged in
  if (!user) {
    return (
      <div className="text-center py-10">
        <AlertCircle className="mx-auto h-12 w-12 text-destructive mb-4" />
        <h1 className="text-2xl font-semibold mb-2">{t('toast.accessDenied.title')}</h1>
        <p className="text-muted-foreground mb-6">{t('accessDenied.loginRequired.page', { pageName: t('header.videoCall') })}</p>
        <Link href="/login">
          <Button><LogIn className="mr-2 h-4 w-4" /> {t('login.loginButton')}</Button>
        </Link>
      </div>
    );
  }


  return (
    <div className="container mx-auto py-8 px-4 md:px-6">
      <Card className="shadow-xl">
        <CardHeader>
          <CardTitle className="text-3xl font-bold tracking-tight flex items-center">
            <Video className="mr-3 h-8 w-8 text-primary" />
            {t('page.videoCall.title')}
          </CardTitle>
          <CardDescription>
            {isMeetingActive
              ? t('page.videoCall.description.inMeeting', { code: meetingCode })
              : t('page.videoCall.description.joinOrCreate')}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {isMeetingActive ? (
            // --- Meeting Active View ---
            <div className="space-y-4">
              <div className="aspect-video bg-muted rounded-lg overflow-hidden relative border">
                <video ref={videoRef} className="w-full h-full object-cover" autoPlay muted playsInline />
                {hasCameraPermission === false && (
                     <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/50 text-destructive-foreground p-4">
                        <VideoOff className="h-16 w-16 mb-4" />
                        <p className="text-lg font-semibold">{t('page.videoCall.error.permissionDeniedTitle')}</p>
                        <p className="text-center">{t('page.videoCall.error.permissionRequired')}</p>
                    </div>
                )}
                 {hasCameraPermission === null && !isLoading && ( // Show grant request prompt if needed
                    <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/50 text-white p-4">
                        <p className="text-lg font-semibold mb-4 text-center">{t('page.videoCall.permissionNeeded')}</p>
                        {/* The action that led here (start/join) will trigger the request */}
                    </div>
                )}
                {isLoading && (
                     <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/50 text-white p-4">
                        <Loader2 className="h-12 w-12 animate-spin mb-4" />
                        <p>{t('page.videoCall.requestingPermission')}</p>
                    </div>
                )}
              </div>
              {/* Add meeting controls here (mute, screen share, participants etc. - Placeholder for now) */}
               <div className="text-center text-muted-foreground text-sm p-4 border rounded-md">
                  {t('page.videoCall.placeholder.controls')}
               </div>
              <Button onClick={handleLeaveMeeting} variant="destructive" className="w-full">
                {t('page.videoCall.button.leaveMeeting')}
              </Button>
            </div>
          ) : (
            // --- Pre-Meeting View ---
            <div className="flex flex-col md:flex-row gap-6 items-stretch">
              {/* Join Meeting Card */}
              <Card className="flex-1">
                <CardHeader>
                  <CardTitle>{t('page.videoCall.joinTitle')}</CardTitle>
                  <CardDescription>{t('page.videoCall.joinDescription')}</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <Label htmlFor="meetingCode">{t('page.videoCall.label.meetingCode')}</Label>
                    <Input
                      id="meetingCode"
                      placeholder={t('page.videoCall.placeholder.meetingCode')}
                      value={meetingCode}
                      onChange={(e) => setMeetingCode(e.target.value)}
                      maxLength={6} // Match generated code length
                    />
                  </div>
                </CardContent>
                <CardFooter>
                  <Button onClick={handleJoinMeeting} className="w-full" disabled={isLoading}>
                    {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                    {t('page.videoCall.button.joinMeeting')}
                  </Button>
                </CardFooter>
              </Card>

              {/* Start Meeting Card (Court Official Only) */}
              {user.role === 'CourtOfficial' && (
                <Card className="flex-1 border-primary bg-primary/5">
                  <CardHeader>
                    <CardTitle>{t('page.videoCall.startTitle')}</CardTitle>
                    <CardDescription>{t('page.videoCall.startDescription')}</CardDescription>
                  </CardHeader>
                  <CardContent className="text-center">
                    {generatedCode ? (
                         <div className="space-y-3">
                            <p className="text-sm text-muted-foreground">{t('page.videoCall.shareCode')}</p>
                             <div className="flex items-center justify-center space-x-2 p-3 bg-muted rounded-md">
                                <span className="text-2xl font-mono font-bold tracking-widest text-primary">{generatedCode}</span>
                                <Button variant="ghost" size="icon" onClick={copyCodeToClipboard} aria-label={t('page.videoCall.button.copyCode')}>
                                    <Copy className="h-5 w-5" />
                                </Button>
                             </div>
                             <p className='text-xs text-muted-foreground'>{t('page.videoCall.waitingForPermission')}</p>
                         </div>
                    ) : (
                        <Button onClick={handleGenerateCode} className="w-full" disabled={isLoading}>
                          {isLoading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Video className="mr-2 h-4 w-4" />}
                          {t('page.videoCall.button.startMeeting')}
                        </Button>
                    )}
                  </CardContent>
                   <CardFooter></CardFooter> {/* Added footer for consistent structure */}
                </Card>
              )}
            </div>
          )}

          {/* Camera Permission Alert */}
          {hasCameraPermission === false && !isMeetingActive && (
            <Alert variant="destructive">
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>{t('page.videoCall.error.permissionDeniedTitle')}</AlertTitle>
              <AlertDescription>
                {t('page.videoCall.error.permissionInstructions')}
              </AlertDescription>
            </Alert>
          )}
           {/* General Instructions/Info */}
          {!isMeetingActive && (
             <Alert variant="default" className="mt-6">
               <Video className="h-4 w-4" />
               <AlertTitle>{t('page.videoCall.infoTitle')}</AlertTitle>
               <AlertDescription>
                   {t('page.videoCall.infoText')}
                   {user.role === 'CourtOfficial' && ` ${t('page.videoCall.infoTextOfficial')}`}
               </AlertDescription>
             </Alert>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
